.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-right: 40px;
  padding-left: 40px;
  height: 5vh;
  background: #D1E9FF;
}

.content {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  text-align: center;
}

.links {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  text-align: center;
  .github-mark {
    height: 4vh;
    margin-top: 10px;
  }
  .linkedin-icon {
    height: 4vh;
    margin-top: 10px;
    padding-left: 15px;
  }
}

.items {
  font-size: 1.5vw;
  padding-left: .5vw;
  padding-right: .5vw;
  a {
    color: black;
    text-decoration: none;
  }
}